export default {
  namespaced: true,
  // 
  state: {
    count: 0,
  },
  mutations: {
    //  mutation
    INCREMENT(state) {
      state.count++;
    },
    DECREMENT(state) {
      state.count--;
    },
  },
  actions: {
    //  action
    increment({ commit }) {
      //  mutation
      commit("INCREMENT");
    },
    decrement({ commit }) {
      //  mutation
      commit("DECREMENT");
    },
    //  action
    incrementIfOdd({ commit, state }) {
      if (state.count % 2 == 1) {
        commit("INCREMENT");
      }
    },
    //  action
    incrementAsync({ commit }) {
      // action
      setTimeout(() => {
        commit("INCREMENT");
      }, 1000);
    },
  },
  getters: {
    evenOrOdd(state) {
      return state.count % 2 == 0 ? "" : "";
    },
  },
};
